
:root {
    --player-label-color: #EDA6FF;
}


.App {
    text-align: center;
}

.App-header{
    background-color : cornflowerblue;
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 14vh;
}

.Team-header{
    background-color : cornflowerblue;
    display: flex;
    align-items: center;
    height: 8vh;
}

.Match-header{
    background-color : cornflowerblue;
    display: flex;
    align-items: center;
    height: 8vh;
}
.App-main{
    background-color :  #045c5a;
    min-height: 90vh;
    max-height: 96vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Team-main{
    background-color :  #045c5a;
    min-height: 90vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly
}

.team-logo-header{
    height : 5rem;
    width: 5rem;
}

.score-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 0;
    width : 100%;
    height: 3.5rem;
    font-size: 2 rem;
    font-weight: 600px;
}

.Match-main{
    background-color :  #045c5a;
    min-height: 90vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly
}

.match-stats-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: aquamarine;
    min-width: 50rem;
    min-height: 30rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 40px;
    color: white;
    border-radius: 90px;
}
.tab{
    background-color : whitesmoke;
    border-radius: 10px;
    overflow: 'hidden';
    height : 40vh;
    width: 30vw;
    margin-top: 0vh;
    margin-bottom: 5vh;
    padding: 4px;
    text-align: center;
    color: #045c5a;
    font-size: 50px;
}

.tab-cell{
    border : 1px solid;
    border: collape;
    border-color: black;
}
.tab-body{
    color:#045c5a;
    text-justify: center;
    font-size:25px;
}

.tab-logo {
    border: 1px solid;
    border-color: black;
    border: collapse;
    justify-items: center
}

.teamLogo{
   width : 30px;
   height : 30px;
   border-radius: 100%;
}

.upcoming{
    flex-direction:column;
    align-items: center;
    justify-content: center;
    background-color: var(--player-label-color);
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-radius: 90px;
    margin: 1vh 2vw;
    width : 9rem;
    height: 3.5rem;
    font-size: 0.7rem;
}


.player-table-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    max-height: 40%;
    border-radius: 10px;
    max-width : 50vw;
}

.team-table-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 40%;
    border-radius: 10px;
    max-width : 50vw;
    margin-right: 1rem;
}


.player-tab{
    background-color : whitesmoke;
    border-radius: 10px;
    overflow: 'hidden';
    height : 40vh;
    width: 30vw;
    padding: 4px;
    margin-top: 0;
    text-align: center;
    color: #045c5a;
    font-size: 20px;
    overflow: scroll;
}

.team-tab{
    background-color : whitesmoke;
    border-radius: 10px;
    overflow: 'hidden';
    height : 40vh;
    width: 30vw;
    padding: 4px;
    margin-top: 0vh;
    text-align: center;
